import React from 'react';
import { Modal, Button } from 'react-bootstrap';

function ProjectModal(props) {
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {props.title}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body style={modalBodyStyle}>
        <p>{props.description}</p>

        <img
          className="ml-2 img-fluid"
          src={props.image}
          alt=" 🖼️ goes here"
          style={imgStyle}
        />
        {/* if image3 exisist ? render <img> : if not null. */}
        {props.image3 ? (
          <img
            className="ml-2 img-fluid"
            src={props.image3}
            alt=" 🖼️ goes here"
            style={imgStyle}
          />
        ) : null}
        <hr />
        <p>
          {' '}
          <span className="category"> Utilized: {props.utilized} </span>{' '}
        </p>
        <a
          className="btn btn-primary"
          href={props.github}
          target="_blank"
          rel="noreferrer"
        >
          View Code
        </a>
        <a
          className="btn btn-primary"
          href={props.url}
          target="_blank"
          rel="noreferrer"
        >
          Deployed Link
        </a>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={props.onHide}>Close</Button>
      </Modal.Footer>
    </Modal>
  );
}

const imgStyle = {
  objectFit: 'contain',
  maxHeight: '400px',
  borderRadius: '17px',
  marginBottom: '10px',
};
const modalBodyStyle = {
  justifyContent: 'center',
};

export default ProjectModal;
